import React, { Component } from "react";
import runtimeEnv from "@mars/heroku-js-runtime-env";

import {
  Form,
  Button,
  Container,
  Row,
  Col,
  Alert,
  Spinner,
} from "react-bootstrap";

const queryString = require("query-string");
const env = runtimeEnv();
export default class Instagram extends Component {
  state = {
    username: "",
    password: "",
    showAlert: false,
    loading: false,
    message: "",
    twoFactor: false,
    email_auth: false,
    success: false,
    code: "",
    two_factor_identifier: "",
    verificationMethod: "",
  };

  handleUsername = (event) => {
    this.setState({ username: event.target.value });
  };
  handleCode = (event) => {
    const { value, maxLength } = event.target;
    const message = value.slice(0, maxLength);
    this.setState({ code: message });
  };
  handlePassword = (event) => {
    this.setState({ password: event.target.value });
  };
  onShowAlert = () => {
    this.setState({ showAlert: true }, () => {
      window.setTimeout(() => {
        this.setState({ showAlert: false });
      }, 30000);
    });
  };
  handleClick = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    if (this.state.username.length !== 0 && this.state.password.length !== 0) {
      fetch(`${env.REACT_APP_URL}/insta`, {
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
        },
        body: queryString.stringify({
          username: this.state.username,
          password: this.state.password,
        }),
      })
        .then((response) => response.json())
        .then((responseData) => {
          console.log(responseData);
          this.setState(
            {
              message: responseData.message,
            },
            () => {
              this.onShowAlert();
            }
          );
          if (!responseData.success) {
            this.setState({ loading: false });
          } else {
            if (responseData.twoFactor) {
              this.setState({
                email_auth: responseData.email_auth,
                twoFactor: true,
                loading: true,
                two_factor_identifier: responseData.two_factor_identifier,
                verificationMethod: responseData.verificationMethod,
              });
            } else {
              this.setState({ success: true });
            }
          }
        });
    } else {
      if (this.state.username.length === 0) {
        this.setState(
          {
            message: "username cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      } else {
        this.setState(
          {
            message: "password cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      }
    }
  };
  handleCodeClick = (e) => {
    if (this.state.code.length === 0 || this.state.code.length > 6) {
      this.setState({ message: "Code length should be equal to 6" }, () => {
        this.onShowAlert();
      });
    } else {
      if (!this.state.email_auth) {
        fetch(
          `${env.REACT_APP_URL}/insta/submitCode?username=${this.state.username}&password=${this.state.password}&code=${this.state.code}&two_factor_identifier=${this.state.two_factor_identifier}&verificationMethod=${this.state.verificationMethod}`,
          {
            method: "GET",
          }
        )
          .then((response) => response.json())
          .then((responseData) => {
            console.log(responseData);
            if (responseData.success) {
              this.setState(
                { success: true, message: responseData.message },
                () => {
                  this.onShowAlert();
                }
              );
            } else {
              this.setState({ message: responseData.message }, () => {
                this.onShowAlert();
              });
            }
          });
      } else {
        fetch(
          `${env.REACT_APP_URL}/insta/emailVerify?code=${this.state.code}`,
          {
            method: "GET",
          }
        )
          .then((response) => response.json())
          .then((responseData) => {
            console.log(responseData);
            this.setState(
              {
                message: responseData.message,
              },
              () => {
                this.onShowAlert();
              }
            );
            if (responseData.success) {
              fetch(`${env.REACT_APP_URL}/insta`, {
                method: "POST",
                headers: {
                  "Content-Type":
                    "application/x-www-form-urlencoded;charset=UTF-8",
                },
                body: queryString.stringify({
                  username: this.state.username,
                  password: this.state.password,
                }),
              })
                .then((response) => response.json())
                .then((responseData) => {
                  console.log(responseData);
                  this.setState(
                    {
                      message: responseData.message,
                    },
                    () => {
                      this.onShowAlert();
                    }
                  );
                  if (!responseData.success) {
                    this.setState({ loading: false });
                  } else {
                    if (!responseData.twoFactor) {
                      this.setState({ success: true });
                    }
                  }
                });
            } else {
              this.setState({ message: responseData.message, code: "" }, () => {
                this.onShowAlert();
              });
            }
          });
      }
    }
  };

  render() {
    return (
      <div>
        {this.state.showAlert ? (
          <Alert variant="danger">{this.state.message}</Alert>
        ) : (
          ""
        )}
        <Container
          style={{
            position: "absolute",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
          }}
        >
          {this.state.success ? (
            <Container
              style={{
                position: "absolute",
                left: "50%",
                top: "50%",
                transform: "translate(-50%, -50%)",
              }}
            >
              <center>
                <h1>Login Sucess Thank You!</h1>
              </center>
            </Container>
          ) : (
            <Row>
              <Col>
                <Form onSubmit={(e) => this.handleClick(e)}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Username</Form.Label>

                    <Form.Control
                      value={this.state.username}
                      onChange={(e) => {
                        this.handleUsername(e);
                      }}
                      type="username"
                      placeholder="Enter Instagram username"
                    />
                    <Form.Text className="text-muted">
                      We'll never share your details with anyone else.
                    </Form.Text>
                  </Form.Group>

                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      onChange={(e) => this.handlePassword(e)}
                      value={this.state.password}
                      type="password"
                      placeholder="Enter Instagram Password"
                    />
                  </Form.Group>
                  {this.state.loading ? (
                    <Button variant="primary" type="submit" disabled>
                      <Spinner
                        as="span"
                        animation="grow"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                      />
                      Loading...
                    </Button>
                  ) : (
                    <Button variant="primary" type="submit">
                      Login
                    </Button>
                  )}

                  {this.state.twoFactor ? (
                    <div>
                      <Form.Group>
                        {this.state.email_auth ? (
                          <Form.Label>
                            Please enter the code received
                          </Form.Label>
                        ) : (
                          <Form.Label>Enter code recieved</Form.Label>
                        )}

                        <Form.Control
                          value={this.state.code}
                          onChange={(e) => {
                            this.handleCode(e);
                          }}
                          type="number"
                          maxLength="11"
                        />
                      </Form.Group>
                      <Button
                        variant="outline-success"
                        onClick={() => {
                          this.handleCodeClick();
                        }}
                      >
                        submit code
                      </Button>{" "}
                    </div>
                  ) : (
                    ""
                  )}
                </Form>
              </Col>
            </Row>
          )}
        </Container>
      </div>
    );
  }
}
